<template>
  <div class="project pageSpace h100">
    <div style="float: right">
      <a-tooltip title="配置参考">
        <span @click="openReference">
          <img src="/static/imagin/ling.png" height="40" width="40">
        </span>
      </a-tooltip>
    </div>
    <my-breadcrumb></my-breadcrumb>
    <div class="pageContent" style="position:relative">
      <router-view />
      <referenceModal :visible="referenceVisible" :dragable="true" @close="close"></referenceModal>
    </div>
  </div>
</template>
<script>
import referenceModal from "@/components/myModal/referenceModal.vue";
export default {
  name: "project",
  components: {referenceModal},
  data(){
    return {
      referenceVisible:false
    }
  },
  methods:{
    openReference(){
      this.referenceVisible = !this.referenceVisible;
    },
    close(){
      this.referenceVisible = false;
    }
  }
};
</script>
<style>
.lingCls {
  cursor: pointer;
  position: fixed;
  right: 30px;
  bottom: 30px;
}
</style>